// Initial

// Document Ready
$(window).ready(function(){
	
  // set all parallax
  $('.parallax').parallax();

  loading('start');

  if ( $(window).width() < 993 ) {
    // collapse navigation once clicked
    $('[routerlink]').on('click', function(){
      $('.navbar-toggler').click(); //bootstrap 4.x
    });
  }

});

// Document Load
$(window).on('load',function(){
  // console.log('master js loaded!');
  loading('end');

    // TEMPORARY
    current_link();
    $('.nav-link').on('click', function() {
      current_link();
    })
    function current_link() {
      // active pages, add 'active' class on navbar item
      var url = window.location;
      $('.nav-item a').removeClass('active');
      $('.nav-item a').filter(function() {
           return this.href == url;
      }).addClass('active');
    }
    // ------------END-OF-TEMPORARY----

});

